.viewSourceCont {
	margin-top: 20px;
}
.viewSecMainCont {
	border: 1px solid #e5e5e5;
	/* border-radius: 4px; */
	margin-bottom: 25px;
}

.viewTabHeadings {
	border-bottom: 1px solid #ccc;
	margin-top: 20px;
}
.viewTabHeadings li {
	cursor: pointer;
	margin: 0 0.2em 1px 0;
	list-style: none;
	position: relative;
	top: 2px;
	margin: 0 0.2em 1px 0;
	/* border-bottom: 0; */
	padding: 0;
	white-space: nowrap;
	outline: none;
}
.viewTabHeadings li span {
	border: 1px solid #ccc;
	border-bottom: none;
	margin-left: 0.5em;
	padding: 5px 10px;
	font-size: 16px;
	line-height: 1.71428571;
	font-weight: 600;
	background: #e5e5e5;
	color: #555;
}
.viewTabHeadings li span i {
	margin-right: 5px;
}
.viewTabHeadings li.viewTabActive span {
	background-color: #fff;
	border-bottom: 1px solid #fff;
}

.view-child {
	margin-left: 10px;
}
.pure-form {
	font-size: 14px;
}
.pure-form select {
	padding-top: 0px;
	padding-bottom: 0px;
}
.pure-form legend {
	font-weight: bold;
}
.final-view-settings {
	display: none;
}
.pro-features {
	background: #ccc;
	padding: 2px 2px;
	border-radius: 4px;
}
.pure-form input[type='password'],
.pure-form input[type='email'],
.pure-form input[type='url'],
.pure-form input[type='date'],
.pure-form input[type='month'],
.pure-form input[type='time'],
.pure-form input[type='datetime'],
.pure-form input[type='datetime-local'],
.pure-form input[type='week'],
.pure-form input[type='tel'],
.pure-form input[type='color'],
.pure-form input[type='number'],
.pure-form input[type='search'],
.pure-form input[type='text'],
.pure-form select,
.pure-form textarea {
	padding-top: 0px;
	padding-bottom: 0px;
}
.pure-form select {
	padding-right: 28px;
}

.pure-form input[type='file']:focus,
.pure-form input[type='checkbox']:focus,
.pure-form input[type='radio']:focus {
	outline: none !important;
}

[data-react-beautiful-dnd-drag-handle='0'] {
	cursor: move !important;
	pointer-events: auto !important;
}
/* Tabs */

.react-tabs {
	-webkit-tap-highlight-color: transparent;
	display: flex;
	/* background: #f9f9f9; */
	min-height: 300px;
	padding: 0px;
}

.react-tabs__tab-list {
	border-right: 1px solid #ccc;
	border-bottom-width: 0px;
	margin: 0px;
	padding: 0;
	display: flex;
	flex-direction: column;
	width: 20%;
}

.react-tabs__tab {
	display: inline-block;
	border-bottom: 1px solid #ccc;
	bottom: -1px;
	position: relative;
	list-style: none;
	padding: 6px 12px;
	cursor: pointer;
	margin-bottom: 0px;
	padding: 14px;
}

.react-tabs__tab--selected {
	background: #ececec;
	border-width: 1px;
	color: black;
	border-radius: 0;
}

.react-tabs__tab--disabled {
	color: GrayText;
	cursor: default;
}

.react-tabs__tab:focus {
	box-shadow: 0 0 5px hsl(208, 99%, 50%);
	border-color: hsl(208, 99%, 50%);
	outline: none;
}

.react-tabs__tab:focus:after {
	content: '';
	position: absolute;
	height: 5px;
	left: -4px;
	right: -4px;
	bottom: -5px;
	background: #fff;
}

.react-tabs__tab-panel {
	display: none;
	padding-left: 0px;
}

.react-tabs__tab-panel--selected {
	display: block;
	flex-grow: 1;
}
.react-tabs__tab-panel--selected div:nth-child(odd) {
	background: #fff;
}

.react-tabs__tab-panel--selected div:nth-child(even) {
	background: #f9f9f9;
}

/* /Tabs */
